/* ====================================================
  EDITOR ELEMENT LAYOUT
==================================================== */

.ns-editor .ns-layout-overflow .ns-element {
    opacity: 0.1;
}

.ns-editor.ns-editor-animating .ns-layout-overflow {
    opacity: 0;
}

/* .ns-editor .ns-layout-editable .ns-element-text */::selection {
    color: inherit;
    background: rgba(26, 26, 26, 0.15);
    -webkit-touch-callout: none;
    -webkit-user-drag: none;
    -webkit-tap-highlight-color: transparent;
}

.ns-editor .ns-layout-editable .ns-element-text {
    -webkit-touch-callout: none;
    -webkit-user-drag: none;
    -webkit-tap-highlight-color: transparent;
}



/* ====================================================
  ELEMENT
==================================================== */

div.ns-element {
    position: absolute;
    outline: none;
    cursor: auto;
}

.ns-moveable-element .ns-selectable-element {
    pointer-events: auto;
} 

.ns-element-scale {
    /* -webkit-transform-origin: top left;
    transform-origin: top left; */
    position: relative;
}

.ns-element-mask {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
}

.ns-element-cropbox {
    pointer-events: none;
    position: relative;
}

.ns-element-opacity {
    pointer-events: none;
}

.ns-element-image {
    display: block;
    position: absolute;
    width: 100%;
    height: 100%;
    pointer-events: none;
}

.ns-element-video {
    display: block;
    position: absolute;
    width: 100%;
    height: 100%;
    pointer-events: none;
}

.ns-element-video.select-none
.ns-element-image.select-none {
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    -webkit-touch-callout: none;
    -webkit-user-drag: none;
    -webkit-tap-highlight-color: transparent;
}