/* MOVEABLE */

@layer components {
    .moveable-control-box{
        z-index: 0;
        --bounds-color: rgb(217 70 239) !important;
    }
    .moveable-line, .moveable-control {
        left: 0;
        top: 0;
        will-change: transform;
    }
    .moveable-control {
        @apply rounded-none border-0 bg-transparent -mt-2 -ml-2 !important;
        position: absolute;
        box-sizing: border-box;
        z-index: 10;
    }

    .moveable-control.moveable-origin {
        @apply rounded-full border-blue-500 border-2 !important;
    }

    .moveable-control.moveable-clip-control.moveable-snap-control {
        @apply rounded-sm border-0 bg-amber-500 -mt-1 -ml-1 w-2 h-2 !important;
    }

    .moveable-line.moveable-clip-line {
        @apply bg-amber-500 !important;
        z-index: -1 !important;
    }

    .moveable-line.moveable-direction {
        @apply border-t border-l border-blue-500 /* mix-blend-exclusion */ bg-blue-500 !important;
        border-width: 1px !important;
    }

    .moveable-line {
        @apply bg-blue-500 !important;
        position: absolute;
        width: 1px;
        height: 1px;
        background: #4af;
        transform-origin: 0px 0.5px;
    }
    .moveable-line.moveable-dashed {
        box-sizing: border-box;
        background: transparent !important;
    }
    .moveable-line.moveable-dashed.moveable-horizontal {
        border-top: 1px dashed #4af;
    }
    .moveable-line.moveable-dashed.moveable-vertical {
        border-left: 1px dashed #4af;
    }
    .moveable-line.moveable-dashed:before {
        @apply text-fuchsia-500 !important;
        position: absolute;
        content: attr(data-size);
        font-size: 12px;
        font-weight: bold;
    }

    .moveable-size-value.moveable-gap {
        @apply text-white bg-amber-500 rounded-sm px-1 !important;
        z-index: 6000 !important;
    }

    .moveable-line.moveable-dashed.moveable-horizontal:before {
        left: 50%;
        transform: translateX(-50%);
        bottom: 5px;
    }
    .moveable-line.moveable-dashed.moveable-vertical:before {
        top: 50%;
        transform: translateY(-50%);
        left: 5px;
    }

    .moveable-bold,
    .moveable-target,
    .moveable-vertical {
        @apply bg-fuchsia-500 !important;
        /* background-color: green !important; */
    }

    .moveable-dashed {
        @apply border-amber-500 bg-transparent !important;
        /* border-color: green !important; */
    }
    
    .moveable-line.moveable-rotation-line .moveable-control {
        border-color: #4af;
        background:#fff;
        cursor: alias;
    }
    .moveable-line.moveable-vertical {
        transform: translateX(-50%);
    }
    .moveable-line.moveable-horizontal {
        transform: translateY(-50%);
    }
    .moveable-line.moveable-vertical.moveable-bold {
        width: 1px !important;
    }
    .moveable-line.moveable-horizontal.moveable-bold {
        height: 1px !important;
    }

    .moveable-line.moveable-guideline.moveable-vertical.moveable-bold {
        @apply bg-fuchsia-500 !important;
    }

    .moveable-line.moveable-guideline.moveable-horizontal.moveable-bold {
        @apply bg-fuchsia-500 !important;
    }

    .moveable-line.moveable-guideline.moveable-gap {
        @apply bg-amber-500 !important;
    }

    /* .moveable-control.moveable-origin{
        @apply bg-blue-500 rounded-full !important;
        background: #fff;
        width: 12px;
        height: 12px;
        margin-top: -6px;
        margin-left: -6px;
        pointer-events: none;
    } */

    .moveable-control.moveable-rotation-control {
        @apply bg-blue-500 border-blue-500 rounded-full !important;
    }

    .moveable-group {
        z-index: -1;
    }
    .moveable-area {
        position: absolute;
    }
    .moveable-area-pieces {
        position: absolute;
        top: 0;
        left: 0;
        display: none;
    }
    .moveable-area.moveable-avoid {
        pointer-events: none;
    }
    .moveable-area.moveable-avoid+.moveable-area-pieces {
        display: block;
    }
    .moveable-area-piece {
        position: absolute;
    }

    .moveable-direction {
        @apply pointer-events-auto focus:outline-none !important;
    }

    .moveable-direction:not([class^="moveable-line"]) {
        /* @apply pointer-events-auto bg-gray-50 border-gray-500 rounded-full hover:bg-blue-500 hover:border-blue-200 !important; */
        @apply pointer-events-auto bg-white border-blue-500 rounded-full hover:bg-blue-500 !important;
    }

    .moveable-dragging .moveable-direction:not([class^="moveable-line"]):not(:hover) {
        @apply opacity-0 !important;
    }

    .moveable-small-height .moveable-control:not(.moveable-nw):not(.moveable-e) {
        @apply opacity-0 pointer-events-none !important;
    }

    .moveable-editing-text .moveable-control {
        @apply opacity-0 !important;
    }

    .moveable-animating .moveable-control-box {
        @apply opacity-0 transition transition-all duration-200 ease-in-out !important;
    }

    .cropperContainer .moveable-control-box {
        @apply opacity-0 !important;
    }

    .ns-editor .moveable-direction:not([class^="moveable-line"]):after {
        content: "";
        position: absolute;
        box-sizing: border-box;
        left: 50%;
        top: 50%;
        z-index: -1;
        transform: translate(-50%, -50%) scale(3);
        width: 100%;
        height: 100%;
        border-radius: 7px;
        /* background: #E83474;
        opacity: 0.5;
        transition: 0.3s ease-out; */
    }

    /* .ns-editor .moveable-direction.moveable-e::after,
    .ns-editor .moveable-direction.moveable-w::after {
        transform: translate(-50%, -50%) scaleX(2) scaleY(3) !important;
    } */

    .ns-editor .moveable-direction.moveable-nw::after { transform: translate(-75%, -75%) scaleX(2.5) scaleY(2.5) !important; }
    .ns-editor .moveable-direction.moveable-ne::after { transform: translate(-25%, -75%) scaleX(2.5) scaleY(2.5) !important; }

    .ns-editor .moveable-direction.moveable-e::after { transform: translate(50%, -50%) scaleX(3) scaleY(3) !important; }
    .ns-editor .moveable-direction.moveable-w::after { transform: translate(-150%, -50%) scaleX(3) scaleY(3) !important; }

    /* .ns-editor .moveable-direction.moveable-s::after,
    .ns-editor .moveable-direction.moveable-n::after {
        transform: translate(-50%, -50%) scaleX(3) scaleY(2) !important;
    } */

    .ns-editor .moveable-direction.moveable-sw::after { transform: translate(-75%, -25%) scaleX(2.5) scaleY(2.5) !important; }
    .ns-editor .moveable-direction.moveable-se::after { transform: translate(-25%, -25%) scaleX(2.5) scaleY(2.5) !important; }

    .ns-editor .moveable-direction.moveable-s::after { transform: translate(-50%, 50%) scaleX(3) scaleY(3) !important; }
    .ns-editor .moveable-direction.moveable-n::after { transform: translate(-50%, -150%) scaleX(3) scaleY(3) !important; }


    .moveable-edgeDraggable {
        @apply opacity-0 !important;
    }

    .moveable-edge::after {
        content: "";
        position: absolute;
        box-sizing: border-box;
        left: 50%;
        top: 50%;
        z-index: -1;
        border-radius: 7px;
        width: 100%;
        height: 2px;
        /* background: #E83474;
        opacity: 0.5; */
    }

    .ns-editor .moveable-edge.moveable-s::after,
    .ns-editor .moveable-edge.moveable-n::after {
        top: -3px;
        transform: translate(-50%, -50%) scaleY(5) !important;
    }

    .ns-editor .moveable-edge.moveable-e::after, 
    .ns-editor .moveable-edge.moveable-w::after {
        top: -3px;
        transform: translate(-50%, -50%) scaleY(5) !important;
    }

    /* Right */
    .moveable-direction.moveable-e:not([class^="moveable-line"]) {
        @apply absolute w-2 -mt-3 h-6 cursor-ew-resize !important;
        margin-left: -4px !important;
        border-width: 2px !important;
    }

    /* .moveable-direction.moveable-e:not([class^="moveable-line"]) {
        @apply absolute -mt-1.5 -ml-1.5 cursor-ew-resize w-3 h-3 !important;
        border-width: 2px !important;
    } */

    /* Left */
    .moveable-direction.moveable-w:not([class^="moveable-line"]) {
        @apply absolute w-2 -mt-3 h-6 cursor-ew-resize !important;
        margin-left: -4px !important;
        border-width: 2px !important;
    }

    /* Bottom */
    .moveable-direction.moveable-s:not([class^="moveable-line"]) {
        @apply absolute w-6 -ml-3 h-2 cursor-ns-resize !important;
        margin-top: -4px !important;
        border-width: 2px !important;
    }

    /* Top */
    .moveable-direction.moveable-n:not([class^="moveable-line"]) {
        @apply absolute w-6 -ml-3 h-2 cursor-ns-resize !important;
        margin-top: -4px !important;
        border-width: 2px !important;
    }

    /* Top left */
    .moveable-direction.moveable-nw, .moveable-reverse .moveable-direction.moveable-nw {
        @apply absolute cursor-nwse-resize -mt-1.5 -ml-1.5 w-3 h-3 !important;
        border-width: 2px !important;
    }

    /* Top right */
    .moveable-direction.moveable-ne, .moveable-reverse .moveable-direction.moveable-ne {
        @apply absolute cursor-nesw-resize -mt-1.5 -ml-1.5 w-3 h-3 !important;
        border-width: 2px !important;
    }

    /* Bottom left */
    .moveable-direction.moveable-sw, .moveable-reverse .moveable-direction.moveable-sw {
        @apply absolute cursor-nesw-resize -mt-1.5 -ml-1.5 w-3 h-3 !important;
        border-width: 2px !important;
    }

    /* Bottom right */
    .moveable-direction.moveable-se, .moveable-reverse .moveable-direction.moveable-se {
        @apply absolute cursor-nwse-resize -mt-1.5 -ml-1.5 w-3 h-3 !important;
        border-width: 2px !important;
    }

    /* ====================================================
        TOP BAR
    ==================================================== */

    .triangle-hit-zone:hover::after {
        @apply w-20 h-6;
        content: "";
        position: absolute;
        box-sizing: border-box;
        clip-path: polygon(30% 0%, 70% 0%, 100% 100%, 0% 100%);
        left: 50%;
        top: 50%;
        /* background: #E83474;
        opacity: 0.5; */
        transform: translate(-50%, -50%) scaleY(2) !important;
    }

    .prevent-everything::after {
        @apply fixed w-full h-full top-0 left-0;
        content: "";
        box-sizing: border-box;
        /* background: #E83474;
        opacity: 0.5; */
        left: 50%;
        top: 50%;
        z-index: 1000;
        transform: translate(-50%, -50%) scale(10) !important;
    }
}




