    box-shadow: 0 4px 10px rgba(0,0,0,.75), 0 8px 30px rgba(0,0,0,.5);
} */

.animation-bg-imgblur {
    background-image: 'none' !important;
    position: absolute; 
    top: 0; 
    left: 0; 
    width: 100%; 
    height: 100%; 
    pointer-events: 0; 
    z-index: 0; 
    overflow: visible !important; 
    border-radius: 2em;
}

.animation-bg-imgblur .bg-color,
.animation-bg-imgblur .bg-black{
    width: 200% !important; 
    height: 200% !important; 
    position: absolute; 
    /* border-radius: 100em; */
    animation: rotate 35s linear infinite;
    /* transform: scale(2); */
}

.animation-bg-imgblur .bg-color{
    right: 0; 
    top: 0; 
    filter: blur(40px); 
    z-index: 10; 
    mix-blend-mode: luminosity;
}

.animation-bg-imgblur .bg-black{
    left: 0; 
    bottom: 0; 
    filter: blur(40px); 
    z-index: 1;
    animation-direction: reverse; 
    animation-delay: 10s;
    display: block !important;
}

@keyframes rotate{
0% {transform: rotate(0deg);}
100% {transform: rotate(360deg);}
}



