.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.inline-text_copy--active,
.inline-text_input--active {
  font: inherit;
  color: inherit;
  text-align: inherit;
  text-transform: inherit;
  font-weight: inherit;
  letter-spacing: inherit;
  padding: 0;
  background: none;
  border: none;
  border-bottom: 1px dashed #666666;
}
.inline-text_copy--active {
  cursor: pointer;
}
.inline-text_copy--hidden,
.inline-text_input--hidden {
  display: none;
}
.inline-text_input--active {
  border-bottom: 1px solid #666666;
  text-align: left;
}


/* Customize website's scrollbar like Mac OS
Not supports in Firefox and IE */

/* total width */
.scrollbar-y::-webkit-scrollbar {
    background-color:transparent;
    width:16px;
}

.scrollbar-x::-webkit-scrollbar {
  background-color:transparent;
  width:16px;
}

/* background of the scrollbar except button or resizer */
.scrollbar-y::-webkit-scrollbar-track {
    background-color:transparent
}
.scrollbar-y::-webkit-scrollbar-track:hover {
    background-color:transparent
}

.scrollbar-x::-webkit-scrollbar-track {
  background-color:transparent
}
.scrollbar-x::-webkit-scrollbar-track:hover {
  background-color:transparent
}

/* scrollbar itself */
.scrollbar-y::-webkit-scrollbar-thumb {
    background-color:#a0a0a5;
    border-radius:7px;
    border-top:3px solid transparent;
    border-bottom:3px solid transparent;
    border-left:5px solid transparent;
    border-right:5px solid transparent;
    background-clip: content-box;
}
.scrollbar-y::-webkit-scrollbar-thumb:hover {
    background-color:#8c8c90;
}

.scrollbar-x::-webkit-scrollbar-thumb {
  background-color:#a0a0a5;
  border-radius:7px;
  border-top:5px solid transparent;
  border-bottom:5px solid transparent;
  border-left:3px solid transparent;
  border-right:3px solid transparent;
  background-clip: content-box;
}
.scrollbar-x::-webkit-scrollbar-thumb:hover {
  background-color:#8c8c90;
}

/* set button(top and bottom of the scrollbar) */
.scrollbar-y::-webkit-scrollbar-button {display:none}

.scrollbar-x::-webkit-scrollbar-button {display:none}


.identifier {
  position: absolute;
  padding: 5px;
  background-color: rgba(255, 111, 0, 0.85);
  color: #FFF;
  border: 1px dashed rgba(255, 255, 255, 0.7);
  z-index: 2;
  font-size: 12px;
  margin: 0;
}

.highlighter {
  background: rgba(0, 255, 0, 0.25);
  border: 1px dashed #fff;
  z-index: 1;
  position: absolute;
}

.app-container {
  display: grid;
  grid-template-columns: 3.5rem 1fr 25rem ; 
  grid-template-rows: max-content 1fr max-content;
  grid-template-areas: 
      "sidebar header rightsidebar"
      "sidebar main rightsidebar"
      "sidebar footer rightsidebar";
}

