/* 3.5rem 1fr 16.5rem */

.side-navbar-container {
  grid-area: sidebar;
}

.toolbar-container {
  grid-area: header;
}

.editor-container {
  grid-area: main;
}

.sequence-preview-container {
  grid-area: footer;
}

.element-list-container {
  grid-area: rightsidebar;
}


.property-panel {
  display: grid;
  min-height: 0;
  grid-template-columns: minmax(0, 1fr); /* 3.5rem 1fr 16.5rem */
  grid-template-rows: max-content minmax(0, 1fr);
  grid-template-areas: 
      "options"
      "layers";
}

.property-panel-options {
  grid-area: options
}

.property-panel-layers {
  grid-area: layers;
  min-height: 0;
}